html, body {
  height: 100%;
}

body {
  background: url(https://unsplash.it/1600/1200/?random&blur) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

#overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,0.5);
  pointer-events: none;
}

#text-container {
  position: fixed;
  bottom: 40px;
  left: 60px;
}

#text {
  color: rgba(255,255,255,1);
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 400;
}

#info {
  color: rgba(255,255,255,0.25);
  font-size: 11px;
  font-weight: 300;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  a {
    color: rgba(#BAE8BD, 1);
    text-decoration: none;
    &:hover, &:focus {
      color: #BAE8BD;
    }
  }
}

#dribbble-container {
  position: fixed;
  top: 15%;
  left: 60px;
}

#dribbble-title {
  font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: rgba(255,255,255,1);
  font-size: 16px;
}

#dribbble-list {
  padding: 0;
  list-style: none;
  font-size: 11px;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  li {
    a {
      color: rgba(255,255,255,1);
      text-decoration: none;
      opacity: 0.25;
      transition: (color 250ms ease-out, opacity 250ms ease-out);
      &:hover, &:focus {
        color: rgba(#BAE8BD, 1);
        opacity: 1;
      }
    }
  }
}
